:root {
    // colors //
    --clr-dark: 230 35% 7%;
    --clr-light: 231 77% 90%;
    --clr-white: 0 0% 100%;
    // colors //

    // font-sizes //
    --fs-900: 9.375rem;
    --fs-800: 6.25rem;
    --fs-700: 3.5rem;
    --fs-600: 2rem;
    --fs-500: 1.75rem;
    --fs-400: 1.125rem;
    --fs-300: 1rem;
    --fs-200: 0.875rem;
    // font-sizes //

    // font-families //
    --ff-bellefair: "Bellefair", serif;
    --ff-barlow-cond: 'Barlow Condensed', sans-serif;
    --ff-barlow: "Barlow", sans-serif;
    // font-families //

}

body {
    font-family: var(--ff-barlow);
    font-size: var(--fs-400);
    color: hsl(var(--clr-white));
    background-color: hsl(var(--clr-dark));
}